.tp_panel_container {
  font-size: 0.8125rem;
  background-color: #ddd; }
  .tp_panel_container :global(.row) {
    padding: 7px 0; }
  .tp_panel_container .lang_bar {
    position: relative; }
    .tp_panel_container .lang_bar .selected_lang_bar {
      cursor: pointer;
      display: inline-flex;
      align-items: center; }
      .tp_panel_container .lang_bar .selected_lang_bar img {
        max-width: 20px;
        margin-right: 10px; }
      .tp_panel_container .lang_bar .selected_lang_bar i {
        margin-left: 5px; }
    .tp_panel_container .lang_bar .leftovers_language_bar {
      position: absolute;
      left: 0;
      top: calc(100% + 7.5px);
      padding: 0;
      margin-bottom: 0;
      box-shadow: 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
      visibility: hidden;
      opacity: 0;
      transition: all 0.4s cubic-bezier(0.87, -0.41, 0.19, 1.44);
      -webkit-transform: scale(0.8);
      transform: scale(0.8);
      z-index: 222; }
      .tp_panel_container .lang_bar .leftovers_language_bar > li {
        cursor: pointer;
        padding: 6px 14px;
        display: flex;
        align-items: center; }
        .tp_panel_container .lang_bar .leftovers_language_bar > li img {
          margin-right: 10px;
          max-width: 20px; }
  .tp_panel_container .right_bar > ul > li {
    margin: 0 5px; }
    .tp_panel_container .right_bar > ul > li a {
      color: #444; }
  .tp_panel_container .right_bar > ul > li:last-child {
    margin-right: 0; }
