.nav-bar-ln {
  background-color: #191919;
  box-shadow: 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12); }
  @media screen and (max-width: 767px) {
    .nav-bar-ln .phone-nav-bar {
      display: none; } }
  .nav-bar-ln nav > a {
    padding: 10px 1.3rem;
    display: inline-block;
    color: #fff;
    font-size: .85rem;
    text-decoration: none;
    z-index: 4;
    position: relative; }
    @media screen and (max-width: 480px) {
      .nav-bar-ln nav > a {
        padding: 10px 0.7rem;
        font-size: .65rem; } }
    .nav-bar-ln nav > a:before {
      content: '';
      position: absolute;
      right: 0;
      top: 0;
      bottom: 0;
      margin: auto;
      height: 60%;
      width: 1px;
      background-color: #fce8ed21; }
  .nav-bar-ln nav > a:last-child:before {
    display: none; }
  .nav-bar-ln nav .wave_active {
    position: absolute;
    background-color: #f01b48;
    height: 100%;
    transition: all cubic-bezier(0.075, 0.82, 0.165, 1) 0.4s;
    -webkit-border-radius: 2px;
    -moz-border-radius: 2px;
    border-radius: 2px;
    z-index: 0; }
  .nav-bar-ln .call-us-wrap {
    display: flex;
    align-items: center;
    height: 100%;
    justify-content: flex-end; }
    .nav-bar-ln .call-us-wrap a {
      color: #fff;
      font-size: .85rem; }
