.footer_ovo {
  background-color: #191919;
  color: #fff;
  margin-top: 40px;
  padding: 30px 0 0; }
  .footer_ovo .footer_middle .news-letter {
    display: flex;
    margin-bottom: 20px; }
    .footer_ovo .footer_middle .news-letter input {
      padding: 10px;
      font-size: 0.8125rem;
      border: none;
      width: 100%;
      height: 40px !important;
      margin-bottom: 0 !important; }
    .footer_ovo .footer_middle .news-letter > button {
      border: none !important;
      background: #f01b48 !important;
      color: #fff;
      font-size: 1rem !important;
      margin: 0 !important;
      border-radius: 0;
      height: 40px;
      text-transform: capitalize;
      transition: .4s; }
      .footer_ovo .footer_middle .news-letter > button svg {
        margin: 0 !important; }
  .footer_ovo .footer_right > a {
    display: inline-flex;
    width: 30px;
    height: 30px;
    justify-content: center;
    align-items: center;
    background-color: #f01b48;
    color: #fff;
    border-radius: 50%;
    margin-right: 10px;
    margin-bottom: 10px;
    text-decoration: none; }
  .footer_ovo .footer_right > p {
    font-size: .875rem;
    margin-bottom: 5px; }
    .footer_ovo .footer_right > p > span {
      margin-right: 10px; }
  .footer_ovo h4 {
    text-transform: uppercase;
    margin-bottom: 30px; }
    @media screen and (min-width: 768px) and (max-width: 991px) {
      .footer_ovo h4 {
        font-size: 1.2em; } }
  .footer_ovo .footer_bottom {
    background-color: #383535;
    padding: 7px 0;
    font-size: .775rem;
    margin-top: 30px; }
    .footer_ovo .footer_bottom p {
      margin-bottom: 0; }
    .footer_ovo .footer_bottom :global(.container) {
      display: flex;
      justify-content: space-between; }
      @media screen and (max-width: 576px) {
        .footer_ovo .footer_bottom :global(.container) {
          align-items: center;
          flex-direction: column; } }
